.App {
  text-align: center;
}


ul {
  list-style-type: none;
  display: inline-block;
   width: 100%;
  
}
li{
   border-bottom: solid;
   border-color: lightgrey;
   width: 100%;
   margin: 5px;
   
}

.users-list-container{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  background-color: #341f54;
   
}
  
.users-list-card {
     height: 450px;
     border-radius: 5%;
     text-align: left;
}
div.user-image {
  width: 50px;
  height: 50px;
  margin-right: 10%;
    
}
img.user-image{
  width: 50px;
  height: 50px;
  margin: 5px;
  border-radius: 50%;
}

 
.user-row{
  display: inline;
   margin-right: 5px;
}

.card-body{
  overflow: hidden;
  overflow-y: scroll;
  max-height: 100%;
}